.page-footer {
  background-color: @background-alt-color;
}

.page-footer__map {
  border: none;
  margin-bottom: 37px;
  width: 100%;
  height: 362px;
}

.page-footer__container {
  background-color: @background-color;
}

.page-footer__container--inner-page {
  background-color: @background-alt-color;
}

.page-footer__wrapper {
  .wrapper;
  display: flex;
  flex-direction: column;
}

.page-footer__logo {
  margin: 0 auto;
  margin-bottom: 27px;
}

.page-footer__logo[href] {
  &:hover {
    opacity: 0.8;
  }

  &:focus {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}

.page-footer__social {
  padding-top: 5px;
  padding-bottom: 1px;
}

@media (min-width: @tablet-width) {
  .page-footer__map {
    height: 400px;
    margin-bottom: 36px;
  }

  .page-footer__wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  .page-footer__logo {
    margin: 0;
    margin-top: 21px;
    margin-right: 35px;
  }
}

@media (min-width: @desktop-width) {
  .page-footer__container {
    position: relative;
    width: 1220px;
    margin: 0 auto;
  }

  .page-footer__map {
    margin-bottom: 38px;
  }

  .page-footer__wrapper {
    display: flex;
    justify-content: space-between;
  }
}
