.main-nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  padding: 0;
  display: block;
  width: 65px;
  height: 65px;
  cursor: pointer;
  border: none;
  background-color: transparent;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 23px;
  left: 21px;
  width: 24px;
  height: 2px;
  background-color: #222222;
  box-shadow: 0 8px 0 0 #222222, 0 16px 0 0 #222222;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 31px;
  left: 22px;
  width: 23px;
  height: 2px;
  background-color: #222222;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--closed .main-nav__list {
  display: none;
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav--nojs .main-nav__list {
  display: block;
}
.main-nav__list {
  text-align: center;
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-nav__link {
  display: block;
  color: #111111;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-decoration: none;
  text-transform: uppercase;
  border-bottom: 1px solid #ededed;
  padding-top: 23px;
  padding-bottom: 22px;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs {
    display: flex;
  }
  .main-nav--closed .main-nav__list {
    display: flex;
  }
  .main-nav__item {
    margin-right: 33px;
  }
  .main-nav__link {
    padding: 6px 0;
    border: none;
    font-size: 18px;
    line-height: 25px;
    font-weight: 400;
  }
  .main-nav__link--active {
    position: relative;
  }
  .main-nav__link--active::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: -3px;
    left: 0;
    height: 2px;
    background-color: #68b738;
  }
  .main-nav__link[href]:hover {
    opacity: 0.6;
  }
  .main-nav__link[href]:focus {
    opacity: 0.6;
  }
  .main-nav__link[href]:active {
    opacity: 0.3;
  }
}
@media (min-width: 1300px) {
  .main-nav__item {
    margin-right: 37px;
  }
  .main-nav__link {
    font-size: 20px;
    line-height: 25px;
  }
  .main-nav__link--alt-color {
    color: #ffffff;
  }
  .main-nav__link--alt-color--active::after {
    background-color: #ffffff;
  }
}
