.page-header {
  width: @mobile-width;
  margin: 0 auto;
  padding: 0;
  position: relative;
}

.page-header__logo {
  display: block;
  padding-top: 13px;
  padding-bottom: 9px;
  padding-left: 20px;
  border-bottom: 1px solid @border-color;
}

.page-header__logo[href] {
  &:hover {
    opacity: 0.8;
  }

  &:focus {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}

@media (min-width: @tablet-width) {
  .page-header {
    width: @tablet-width;
    display: flex;
    justify-content: space-between;
    padding-top: 36px;
    margin-bottom: 62px;
  }

  .page-header__logo {
    border: none;
    padding: 0;
  }
}

@media (min-width: @desktop-width) {
  .page-header {
    width: @desktop-width;
    display: flex;
    justify-content: space-between;
    margin-bottom: 113px;
    padding-top: 67px;

    &::before {
      position: absolute;
      content: "";
      width: 720px;
      height: 696px;
      top: 0;
      right: -69px;
      background-color: @background-elements;
      background-image: url("../img/index-background-desktop@1x.jpg");
      background-repeat: no-repeat;
      background-position: 0 0;
      z-index: -10;
    }

    &--inner-page {
      margin-bottom: 84px;

      &::before {
        display: none;
      }
    }
  }
}
